.modal {
  z-index: 9999;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal__overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  position: absolute;
  top: 0;
  left: 0;
}

.modal__content {
  width: 806px;
  height: 558px;
  background-color: #fff;
  border-radius: 5px;
  padding: 12px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__content img {
  width: 100%;
  max-width: 375px;
  height: auto;
  max-height: 478px;
  margin: 40px 0;
  display: block;
